.header-table {
  position: absolute;
  width: 1103px;
  height: 140px;
  top: 0;
  margin-left: -20px;
  border-collapse: collapse;
  border-spacing: 1px;
  z-index: -1;
}

._table-min {
  height: 100px;
}

._hidden-min {
  height: 0px;
}

.table {
  position: absolute;
  width: 1103px;
  height: 2992px;
  top: 0;
  margin-left: -20px;
  border-collapse: collapse;
  border-spacing: 1px;
  z-index: -1;
}

.table-tr {}

.table-td {
  border-left: 1px solid #ededed;
  font-size: 0px;
}

.table-td._right {
  border-right: 1px solid #ededed;
}

.contacts-line {
  position: absolute;
  width: 100%;
  margin: 0;
  padding: 0;
  height: 1px;
  background-color: #ededed;
  border: none;
  top: 2992px;
  z-index: -1;
}

.works-works,
.about-about,
.links-links {
  position: absolute;
  transform: rotate(-90deg);
  width: 50px;
  height: 16px;
  color: #979797;
  font-family: Nunito;
  font-size: 12px;
  font-weight: 800;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 3px;
  z-index: -1;
}

.works-works {
  transform-origin: -15px 77px 0;
}

.about-about {
  transform-origin: 13px 107px 0;
}

.links-links {
  transform-origin: -3px 90px 0;
}